	module.exports = {"data":{"challengeNode":{"title":"Problem 335: Gathering the beans","description":["Whenever Peter feels bored, he places some bowls, containing one bean each, in a circle. After this, he takes all the beans out of a certain bowl and drops them one by one in the bowls going clockwise. He repeats this, starting from the bowl he dropped the last bean in, until the initial situation appears again. For example with 5 bowls he acts as follows:","","","","So with 5 bowls it takes Peter 15 moves to return to the initial situation.","","Let M(x) represent the number of moves required to return to the initial situation, starting with x bowls. Thus, M(5) = 15. It can also be verified that M(100) = 10920.","","Find M(2k+1). Give your answer modulo 79."],"challengeType":5,"fields":{"slug":"/coding-interview-prep/project-euler/problem-335-gathering-the-beans","blockName":"Project Euler","tests":[{"text":"<code>euler335()</code> should return 5032316.","testString":"assert.strictEqual(euler335(), 5032316, '<code>euler335()</code> should return 5032316.');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"function euler335() {\n  // Good luck!\n  return true;\n}\n\neuler335();","head":"","tail":""},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/project-euler/problem-336-maximix-arrangements","id":"5900f4bd1000cf542c50ffce"},"slug":"/coding-interview-prep/project-euler/problem-335-gathering-the-beans"}}

